/// <reference types="@workadventure/iframe-api-typings" />

// Никаких import'ов, чтобы не было проблем с бандлингом

WA.onInit().then(() => {
    console.log("=== ROOF SCRIPT LOADED ===");

    const AREA_NAME = "ZoneHouse1";

    // Все верхние слои, которые могут закрывать персонажа
    const ROOF_LAYERS = [
        "TileSloy",
        "TileSloy2",
        "OnMebel",
        "ZaMebel",
        "Mebel",
        "wallss",
        "InvisibleWalls"
    ];

    const hideRoof = () => {
        console.log("=== ENTERING BUILDING - HIDING ROOF ===");
        ROOF_LAYERS.forEach(layer => {
            console.log("Hiding layer:", layer);
            WA.room.hideLayer(layer);
        });
    };

    const showRoof = () => {
        console.log("=== LEAVING BUILDING - SHOWING ROOF ===");
        ROOF_LAYERS.forEach(layer => {
            console.log("Showing layer:", layer);
            WA.room.showLayer(layer);
        });
    };

    // При входе/выходе из зоны
    WA.room.area.onEnter(AREA_NAME).subscribe(hideRoof);
    WA.room.area.onLeave(AREA_NAME).subscribe(showRoof);

    console.log("Listening for area:", AREA_NAME);
});

export {};
